
import { defineComponent } from "vue";
import IImagem from "../interface/IImagem";

export default defineComponent({
  name: "Sobre",
  data() {
    return {
      source: require("../assets/img/moderna-mesas.jpg") as String,
      descr: "Mesas na entrada da hamburgueria" as String,
      imagens: [
        {
          address: require("../assets/img/moderna-mesas.jpg"),
          alt: "Mesas na entrada da hamburgueria",
        },
        {
          address: require("../assets/img/moderna-mesas-fundo.jpg"),
          alt: "Bancada entrada da hamburgueria",
        },
        {
          address: require("../assets/img/moderna-bancada.jpg"),
          alt: "Mesas no fundo da hamburgueria",
        },
        { address: require("../assets/img/banheiros.jpg"), alt: "Banheiros da hamburgueria" },
      ] as IImagem[],
    };
  },
  methods: {
    alterarImagem(adress: String, descript: String): void {
      this.source = adress;
      this.descr = descript;
    },
  },
});
